@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);

*{box-sizing:border-box;margin:0;padding:0}.light{--background-color: #FFFFFF;--color-text: #000000;--color-primary: #cc0000;--color-secondary: #79d3df;--color-study-case: #270f3508}.dark{--background-color: #270f35;--color-text: #FFFFFF;--color-primary: #660000;--color-secondary: #10c5dd;--color-study-case: #FFFFFF30}.App{padding:0 25px;font-family:"Quicksand",sans-serif;background-color:var(--background-color);color:var(--color-text);height:100vh;transition:.3s}h3{margin:20px 0}p{margin:5px 0;white-space:pre-wrap}a{text-decoration:none;color:var(--color-text);outline-style:none}
.light{--background-color: #FFFFFF;--color-text: #000000;--color-primary: #cc0000;--color-secondary: #79d3df;--color-study-case: #270f3508}.dark{--background-color: #270f35;--color-text: #FFFFFF;--color-primary: #660000;--color-secondary: #10c5dd;--color-study-case: #FFFFFF30}.Navbar{display:flex;justify-content:space-around;align-items:center;height:80px;border-bottom:1px solid var(--color-primary);transition:.3s}.Navbar__title{width:50%;font-size:2rem;font-weight:bold}
.light{--background-color: #FFFFFF;--color-text: #000000;--color-primary: #cc0000;--color-secondary: #79d3df;--color-study-case: #270f3508}.dark{--background-color: #270f35;--color-text: #FFFFFF;--color-primary: #660000;--color-secondary: #10c5dd;--color-study-case: #FFFFFF30}.DayNightButton{border-style:none;background-color:var(--background-color);outline-style:none;cursor:pointer;transition:.3s}.DayNightButton__image{height:40px;width:40px}
.ClientCard{margin-top:20px;width:18rem;min-height:10rem;padding:15px;border:1px solid #00000000;box-shadow:0 0 5px var(--color-secondary);border-radius:10px}.ClientCard:hover{border:1px solid var(--color-secondary)}.ClientCard__author{margin:0}
.ClientListCard{display:flex;justify-content:space-around;flex-wrap:wrap;list-style:none}
.StudyCase{margin:50px;padding:10px;border-radius:10px;box-shadow:0 0 10px var(--color-study-case);background-color:var(--color-study-case)}

